import {useEffect, useState} from 'react';
import api from '../../utils/axios';
import toast, {Toaster} from 'react-hot-toast';
import {
    Grid,
    makeStyles,
    Card,
    CardContent,
    MenuItem,
    InputLabel,
    Select,
    CardActions,
    Button,
    CardHeader,
    FormControl
} from '@material-ui/core';

import {Multiselect} from 'multiselect-react-dropdown';
import {useDropzone} from 'react-dropzone';
import Cookies from 'js-cookie';

import BackspaceIcon from '@material-ui/icons/Backspace';
import {Formik, Form, Field} from 'formik';
import * as Yup from 'yup';
import {TextField} from 'formik-material-ui';
import {useHistory, useParams} from 'react-router-dom';
import {baseUrl} from '../../utils/constants/constants';

const useStyle = makeStyles((theme) => ({
    padding: {
        padding: theme.spacing(5)
    },
    button: {
        margin: theme.spacing(1),
        borderRadius: '1rem',
        height: '3rem',
        padding: '0 30px'
    },
    BackButton: {
        borderRadius: '1rem',
        height: '2rem',
        background: '#DE5B27',
        position: 'absolute',
        top: '10rem',
        right: '5rem'
    }
}));

const EditProduct = () => {
    let {productId} = useParams();
    const history = useHistory();
    const [categoryData, setCategoryData] = useState([]);
    const [subCategoryData, setSubCategoryData] = useState([]);
    const [productData, setProductData] = useState([]);
    const [selected, setSelected] = useState([]);
    const [colour, SetColour] = useState([]);
    const [size, setSize] = useState([]);
    let objectArray = [{key: 'Warranty Guarantee'}, {key: 'Featured'}, {key: 'Top Selling'}, {key: 'Tax'}, {key: 'Shipping Charges'}];
    const [token] = useState(Cookies.get('strAccessToken'));
    let headers = {
        headers: {
            authorization: `Bearer ${token}`
        }
    };
    const onSubmit = async (values) => {
        try {
            const formData = new FormData();
            for (let i = 0; i < files.length; i++) {
                formData.append('files', files[i]);
            }
            let wg = false,
                ftd = false,
                ts = false,
                tx = false,
                sc = false;
            for (let i = 0; i < selected.length; i++) {
                if (selected[i]['key'] === 'Warranty Guarantee' && wg === false) {
                    wg = true;
                    selected[i]['key'] === 'Warranty Guarantee'
                        ? formData.append('blnWarrantyGuarantee', true)
                        : formData.append('blnWarrantyGuarantee', false);
                }
                if (selected[i]['key'] === 'Featured' && ftd === false) {
                    ftd = true;
                    selected[i]['key'] === 'Featured' ? formData.append('blnFeatured', true) : formData.append('blnFeatured', false);
                }
                if (selected[i]['key'] === 'Top Selling' && ts === false) {
                    ts = true;
                    selected[i]['key'] === 'Top Selling' ? formData.append('blnTopSelling', true) : formData.append('blnTopSelling', false);
                }
                if (selected[i]['key'] === 'Tax' && tx === false) {
                    tx = true;
                    selected[i]['key'] === 'Tax' ? formData.append('blnTax', true) : formData.append('blnTax', false);
                }
                if (selected[i]['key'] === 'Shipping Charges' && sc === false) {
                    sc = true;
                    selected[i]['key'] === 'Shipping Charges'
                        ? formData.append('blnShippingCharges', true)
                        : formData.append('blnShippingCharges', false);
                }
            }
            formData.append('intCategoryId', values.strCategoryId);
            formData.append('intSubCategoryId', values.strSubCategoryId);
            formData.append('strProductName', values.strProductName);
            formData.append('strProductDescription', values.strProductDescription);
            formData.append('strProductColourId', values.strProductColourId);
            formData.append('strProductPrice', values.strProductPrice);
            formData.append('strProductSizeId', values.strProductSizeId);
            formData.append('strShippingDays', values.strShippingDays);
            formData.append('chrStatus', values.chrStatus);
            formData.append('intProductPk', productId);
            const response = await api.put('products/updateProduct', formData, headers);
            toast('Updated successFully.');
            backButton();
            return response.data['message'] ? response.data['message'] : response.data;
        } catch (error) {
            console.log(`error`, error);
            if (error.error === 'PRODUCT_ALREADY_EXIST') {
                toast('Product is already exist in the table !', {
                    style: {
                        backgroundColor: '#EE3434'
                    }
                });
            } else if (error.error === 'KEY_MISSING_PRODUCT_MEDIA') {
                toast('Add at least one Media for Product', {
                    style: {
                        backgroundColor: '#EE3434'
                    }
                });
            } else {
                toast('Something Went Wrong !', {
                    style: {
                        backgroundColor: '#EE3434'
                    }
                });
            }
        }
    };

    async function fetchColourGetData() {
        const response = await api.post('colours/getColour');
        SetColour(response.data);
        return response.data;
    }
    async function fetchSizeGetData() {
        const response = await api.post('sizes/getSize');
        setSize(response.data);
        return response.data;
    }

    async function fetchCategoryGetData() {
        const response = await api.post('productCategory/getProductCategory');
        setCategoryData(response.data);
        return response.data;
    }
    let handleSubUnderCategory = async (value) => {
        if (value !== undefined && value !== 0) {
            const response = await api.post('productSubCategory/getProductSubCategory', {
                intCategoryId: value
            });
            response.data.message !== 'NO_SUB_CATEGORY_UNDER_THIS_CATEGORY_ID' ? setSubCategoryData(response.data) : setSubCategoryData([]);
            return response.data;
        } else {
            setSubCategoryData([]);
        }
    };

    useEffect(() => {
        let fetchProductGetData = async () => {
            try {
                const response = await api.post('products/getProduct', {
                    intProductPk: productId
                });
                setProductData(response.data[0]);
                return response.data;
            } catch (error) {
                console.log('error :>> ', error.message);
            }
        };

        fetchProductGetData();
        fetchColourGetData();
        fetchSizeGetData();
        fetchCategoryGetData();
        handleSubUnderCategory(productData.strCategoryId);
    }, [productId, productData.strCategoryId]);
    const backButton = () => {
        let path = `/viewProducts`;
        history.push(path);
    };
    const CategoryList = categoryData.map((item) => ({
        intProductCategoryPk: item.intProductCategoryPk,
        strProductCategoryName: item.strProductCategoryName
    }));
    const SubCategoryList = subCategoryData.map((item) => ({
        intProductSubCategoryPk: item.intProductSubCategoryPk,
        strProductSubCategoryName: item.strProductSubCategoryName
    }));
    const ColourList = colour.map((item) => ({
        intColourPk: item.intColourPk,
        strColourName: item.strColourName
    }));

    const SizeList = size.map((item) => ({
        intSizePk: item.intSizePk,
        strSizeName: item.strSizeName
    }));
    let arrCheckBox = [];
    if (productData.blnWarrantyGuarantee === true) arrCheckBox.push({key: 'Warranty Guarantee'});
    if (productData.blnFeatured === true) arrCheckBox.push({key: 'Featured'});
    if (productData.blnTopSelling === true) arrCheckBox.push({key: 'Top Selling'});
    if (productData.blnTax === true) arrCheckBox.push({key: 'Tax'});
    if (productData.blnShippingCharges === true) arrCheckBox.push({key: 'Shipping Charges'});
    const initialValues = {
        strCategoryId: productData.strCategoryId,
        strSubCategoryId: productData.strSubCategoryId,
        strProductName: productData.strProductName,
        strProductDescription: productData.strProductDescription,
        strProductColourId: productData.strProductColourId,
        strProductPrice: productData.strProductPrice,
        strProductSizeId: productData.strProductSizeId,
        strShippingDays: productData.strShippingDays,
        chrStatus: productData.chrStatus,
        arrCheckBox: arrCheckBox,
        files: productData.arrProductMedia
    };

    let validationSchema = Yup.object().shape({
        strCategoryId: Yup.number().required('Required'),
        strSubCategoryId: Yup.number().required('Required'),
        strProductName: Yup.string().required('Required'),
        strProductDescription: Yup.string().required('Required'),
        strProductPrice: Yup.number().required('Required'),
        strShippingDays: Yup.string().required('Required'),
        arrCheckBox: Yup.array().required('Required')
    });
    const classes = useStyle();

    const dropzoneContainer = {
        flex: '1',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: '20px',
        borderWidth: '2px',
        borderRadius: '2px',
        borderColor: '#eeeeee',
        borderStyle: 'dashed',
        backgroundColor: '#fafafa',
        color: '#bdbdbd',
        outline: 'none',
        transition: 'border .24s ease-in-out'
    };
    const thumbsContainer = {
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
        marginTop: 16
    };

    const thumb = {
        display: 'inline-flex',
        borderRadius: 2,
        border: '1px solid #eaeaea',
        marginBottom: 8,
        marginRight: 8,
        width: 100,
        height: 100,
        padding: 4,
        boxSizing: 'border-box'
    };

    const thumbInner = {
        display: 'flex',
        minWidth: 0,
        overflow: 'hidden'
    };

    const img = {
        display: 'block',
        width: 'auto',
        height: '100%'
    };
    const [files, setFiles] = useState([]);
    const [strError, setStrError] = useState('');
    const Previews = (props) => {
        const {getRootProps, getInputProps} = useDropzone({
            accept: ['image/*', 'video/*'],
            maxSize: '5242880',
            maxFiles: 5,
            onDrop: (acceptedFiles, rejectedFiles) => {
                let errorAlreadyOccurred = false;
                setStrError(
                    rejectedFiles.map((file) => {
                        if (file.errors[0].message === 'Too many files' && errorAlreadyOccurred === false) {
                            errorAlreadyOccurred = true;
                            return <p style={{color: 'red'}}>Only 5 medias allowed</p>;
                        } else if (file.errors[0].message === 'File is larger than 5242880 bytes') {
                            return <p style={{color: 'red'}}>{file.file.name + file.errors[0].message + ' - File is larger than 5MB'}</p>;
                        }
                        return null;
                    })
                );
                setFiles(
                    acceptedFiles.map((file) =>
                        Object.assign(file, {
                            preview: URL.createObjectURL(file)
                        })
                    )
                );
            }
        });

        const thumbs = files.map((file) => (
            <div style={thumb} key={file.name}>
                <div style={thumbInner}>
                    <img src={file.preview} style={img} alt="" />
                </div>
            </div>
        ));

        useEffect(
            () => () => {
                // Make sure to revoke the data uris to avoid memory leaks
                files.forEach((file) => URL.revokeObjectURL(file.preview));
            },
            []
        );

        return (
            <section className="container">
                <div style={dropzoneContainer} {...getRootProps()}>
                    <p style={{}}>{}</p>
                    <input {...getInputProps()} />
                    <p style={{color: 'blue'}}>Drag 'n' drop some files here, or click to select files</p>
                </div>
                <aside style={thumbsContainer}>{thumbs}</aside>
            </section>
        );
    };
    return (
        <Grid container justify="center" spacing={1}>
            <Grid item md={12}>
                <Toaster
                    toastOptions={{
                        className: '',
                        style: {
                            borderRadius: '1rem',
                            padding: '1rem',
                            fontSize: '1rem',
                            fontWeight: 600,
                            color: '#ffff',
                            backgroundColor: '#8BCC51'
                        }
                    }}
                />
                <Card className={classes.padding}>
                    <CardHeader title={`Edit Product ${productData.strProductName}`} />
                    <Button variant="contained" className={classes.BackButton} title="Back to Products" onClick={backButton}>
                        <BackspaceIcon />
                    </Button>
                    <Formik enableReinitialize initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit}>
                        {({dirty, isValid, values, handleChange, handleBlur, setFieldValue}) => {
                            console.log(dirty, isValid);
                            return (
                                <Form id="updateForm">
                                    <CardContent>
                                        <Grid item container spacing={1} justify="center">
                                            <Grid item xs={12} sm={6} md={6}>
                                                <FormControl fullWidth variant="outlined">
                                                    <InputLabel id="demo-simple-select-outlined-label">Category</InputLabel>
                                                    <Select
                                                        label="Category"
                                                        onChange={handleChange}
                                                        onClick={(event) => handleSubUnderCategory(event.target.dataset.value)}
                                                        onBlur={handleBlur}
                                                        value={String(values.strCategoryId)}
                                                        name="strCategoryId"
                                                    >
                                                        {CategoryList.map((item) => (
                                                            <MenuItem key={item.intProductCategoryPk} value={item.intProductCategoryPk}>
                                                                {item.strProductCategoryName}
                                                            </MenuItem>
                                                        ))}
                                                    </Select>
                                                </FormControl>
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <FormControl fullWidth variant="outlined">
                                                    <InputLabel id="demo-simple-select-outlined-label">Sub Category</InputLabel>
                                                    <Select
                                                        label="Sub Category"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={String(values.strSubCategoryId)}
                                                        name="strSubCategoryId"
                                                    >
                                                        {SubCategoryList.map((item) => (
                                                            <MenuItem
                                                                key={item.intProductSubCategoryPk}
                                                                value={item.intProductSubCategoryPk}
                                                            >
                                                                {item.strProductSubCategoryName}
                                                            </MenuItem>
                                                        ))}
                                                    </Select>
                                                </FormControl>
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <Field
                                                    label="Product Name"
                                                    variant="outlined"
                                                    fullWidth
                                                    name="strProductName"
                                                    value={values.strProductName}
                                                    component={TextField}
                                                />
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <Field
                                                    label="Description"
                                                    variant="outlined"
                                                    fullWidth
                                                    name="strProductDescription"
                                                    value={values.strProductDescription}
                                                    component={TextField}
                                                />
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <FormControl fullWidth variant="outlined">
                                                    <InputLabel id="demo-simple-select-outlined-label">Colour</InputLabel>
                                                    <Select
                                                        labelId="demo-simple-select-outlined-label"
                                                        id="demo-simple-select-outlined"
                                                        label="Colour"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={String(values.strProductColourId)}
                                                        name="strProductColourId"
                                                    >
                                                        <MenuItem key="" value="">
                                                            NONE
                                                        </MenuItem>
                                                        {ColourList.map((item) => (
                                                            <MenuItem key={item.intColourPk} value={item.intColourPk}>
                                                                {item.strColourName}
                                                            </MenuItem>
                                                        ))}
                                                    </Select>
                                                </FormControl>
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <Field
                                                    label="Price"
                                                    variant="outlined"
                                                    fullWidth
                                                    name="strProductPrice"
                                                    value={values.strProductPrice}
                                                    component={TextField}
                                                />
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <FormControl fullWidth variant="outlined">
                                                    <InputLabel id="demo-simple-select-outlined-label">Size</InputLabel>
                                                    <Select
                                                        labelId="demo-simple-select-outlined-label"
                                                        id="demo-simple-select-outlined"
                                                        label="Size"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={String(values.strProductSizeId)}
                                                        name="strProductSizeId"
                                                    >
                                                        <MenuItem key="" value="">
                                                            NONE
                                                        </MenuItem>
                                                        {SizeList.map((item) => (
                                                            <MenuItem key={item.intSizePk} value={item.intSizePk}>
                                                                {item.strSizeName}
                                                            </MenuItem>
                                                        ))}
                                                    </Select>
                                                </FormControl>
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <Field
                                                    label="Shipping Days"
                                                    variant="outlined"
                                                    fullWidth
                                                    name="strShippingDays"
                                                    value={values.strShippingDays}
                                                    component={TextField}
                                                />
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <FormControl fullWidth variant="outlined">
                                                    <InputLabel id="demo-simple-select-outlined-label">Status</InputLabel>
                                                    <Select
                                                        labelId="demo-simple-select-outlined-label"
                                                        id="demo-simple-select-outlined"
                                                        label="chrStatus"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={String(values.chrStatus)}
                                                        name="chrStatus"
                                                    >
                                                        <MenuItem key="A" value="A">
                                                            ACTIVE
                                                        </MenuItem>
                                                        <MenuItem key="D" value="D">
                                                            DE-ACTIVE
                                                        </MenuItem>
                                                    </Select>
                                                </FormControl>
                                            </Grid>
                                            <Grid item xs={12} sm={6} md={6}>
                                                <Multiselect
                                                    label="arrCheckBox"
                                                    name="arrCheckBox"
                                                    options={objectArray}
                                                    selectedValues={arrCheckBox}
                                                    onSelect={(selectedAArray) => setSelected(selectedAArray)}
                                                    onRemove={(removedArray) => setSelected(removedArray)}
                                                    displayValue="key"
                                                    showCheckbox={true}
                                                />
                                            </Grid>
                                            <Grid item xs={12} sm={12} md={12}>
                                                {values.files
                                                    ? values.files.map((file) => {
                                                          return (
                                                              <img
                                                                  key={file.strMediaName}
                                                                  style={{width: '10rem', padding: '1rem'}}
                                                                  src={baseUrl + file.strMediaPath.split('souqmuscatBackEnd/')[1]}
                                                                  alt={file.strMediaName}
                                                              />
                                                          );
                                                      })
                                                    : ''}
                                            </Grid>
                                            <Grid item xs={12} sm={12} md={12}>
                                                <InputLabel id="demo-simple-select-outlined-label">Select Images</InputLabel>
                                                <Previews />
                                                {strError ? strError : ''}
                                            </Grid>
                                        </Grid>
                                    </CardContent>
                                    <CardActions>
                                        <Button
                                            disabled={!isValid}
                                            variant="contained"
                                            color="primary"
                                            type="Submit"
                                            className={classes.button}
                                        >
                                            UPDATE
                                        </Button>
                                    </CardActions>
                                </Form>
                            );
                        }}
                    </Formik>
                </Card>
            </Grid>
        </Grid>
    );
};

export default EditProduct;
